import React from 'react'
import section from './section1.png';
import Section from '../Section'


const questions = [
  {
    id: 1,
    q: '你能清楚地說明你任職的衛生所的定位嗎？(如星巴克的定位是「提供好的喝咖啡體驗」、國立博物館的定位是「平等的傳播知識和娛樂給大眾」)'
  },
  {
    id: 2,
    q: '承上題，你清楚在這樣的定位下，需要完成的目標有哪些嗎？'
  },
  {
    id: 3,
    q: '為了達成目標，你清楚在過去半年的工作中的策略嗎？'
  },
]

const index = () => {
  return (
    <Section
      questions={questions}
      level="一"
      order="前"
      subtitle="最重要的事"
      section={section}
      next="下一階段"
      to="/section2"
      type="button"
    />
  )
}

export default index
